@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  list-style: none;
}

p {
  font-size: 18px;
  color: #2e2e2e;
}

img {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  color: #2e2e2e;
}

.btn {
  display: inline-block;
  background: #eee;
  padding: 15px 25px;
  text-decoration: none;
  font-weight: 900;
  border-radius: 4px;
}

.logo {
  position: relative;
  height: 50px;
  width: 50px;
  background: #4CAF50;
  border-radius: 8px;
}

.logo .circle {
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 15px;
  height: 15px;
  background: #F44336;
  border-radius: 40px;
}

.brand-area {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 25px 0;
  text-decoration: none;
}

.brand-area p {
  color: #2e2e2e;
  font-size: 36px;
  margin-left: 5px;
  font-weight: 900;
  padding: 0 10px;
}

@media (max-width: 768px) {
  .brand-area {
    justify-content: center;
  }
}

.navbar {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}

.navbar.fixed {
  position: fixed;
  background: #ecdb43;
  width: 100%;
  left: 0;
  top: 0;
  padding: 0 5%;
  z-index: 10;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
}

.navbar.fixed .brand-area {
  padding: 15px 0;
}

.navbar ul li {
  display: inline-block;
  margin-left: 25px;
}

.navbar ul li a {
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  color: #2e2e2e;
}

.navbar ul li a.btn {
  background: #4caf50;
  color: #fff;
  padding: 10px 15px;
  font-size: 16px;
  border-radius: 4px;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 100px 0;
}

@media (max-width: 1199px) {
  .container {
    width: 100%;
    padding: 100px 25px;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 60px 15px;
  }
}

.title {
  text-align: center;
  margin-bottom: 100px;
}

.title h2 {
  font-size: 48px;
  margin-bottom: 15px;
}

.title p {
  font-size: 24px;
}

@media (max-width: 768px) {
  .title {
    margin-bottom: 60px;
  }

  .title h2 {
    font-size: 36px;
  }

  .title p {
    font-size: 22px;
  }
}

button {
  border: none;
  background: #ebebeb;
  color: #fff;
  padding: 15px 25px;
  font-weight: 900;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 4px;
}

input {
  padding: 10px 15px;
  border: solid 2px #bebebe;
  margin: 5px;
  font-size: 18px;
  border-radius: 4px;
  width: calc(100%/2 - 10px);
}

@media (max-width: 480px) {
  input {
    width: 100%;
  }
}

.alert {
  margin-top: 15px;
  margin-left: 5px;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
}

.alert.error {
  color: #F44336;
}

.alert.success {
  color: #4CAF50;
}

.marker {
  text-align: center;
  padding: 15px 25px;
}

.marker h4,
.marker p {
  color: #2e2e2e;
}

.marker h4 {
  font-size: 28px;
}

.intro-area {
  position: relative;
  background: #ecdb43;
  height: 700px;
}

.intro-area::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: url(/static/media/bg.b1c70438.png);
  background-size: cover;
  background-position: center;
  opacity: 1;
  z-index: 0;
}

.intro-area .info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  text-align: center;
}

.intro-area .info>* {
  flex: 1 1;
}

.intro-area .info .animation {
  position: relative;
  text-align: left;
  flex: 1 1;
  z-index: 1;
}

.intro-area .info .animation img {
  position: relative;
  -webkit-animation: guyAnimated 1s alternate infinite;
          animation: guyAnimated 1s alternate infinite;
}

.intro-area .intro {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 480px;
  z-index: 1;
  flex: 2 1;
}

.intro-time {
  display: flex;
  flex-wrap: wrap;
}

.intro-time .time {
  background: #045fa4;
  margin-right: 10px;
  padding: 10px 25px;
  border-radius: 4px;
  margin-bottom: 25px;
}

.intro-time .time p {
  font-size: 52px;
  font-weight: 700;
  color: #fff;
  text-align: center;
}

.intro-time .time p span {display: block;font-size: 16px;}

.intro p {
  font-weight: 300;
  font-size: 24px;
}

.intro h1 {
  font-size: 52px;
}

.details {
  margin-top: 45px;
}

.details ul li {
  display: inline-block;
  margin-right: 25px;
}

.details ul li p {
  font-size: 26px;
  font-weight: 900;
}

.details ul li p small {
  display: block;
  font-weight: 300;
}

@media (max-width: 768px) {
  .intro-area .info {
    flex-direction: column-reverse;
  }

  .intro-area::after {
    bottom: -25px;
    left: calc(50% - 30px);
    height: 60px;
    width: 60px;
  }

  .intro {
    text-align: center;
  }

  .intro p,
  .details ul li p {
    font-size: 22px;
  }

  .intro h1 {
    font-size: 36px;
  }

  .intro-area .info .animation {
    margin: 25px 0;
  }

  .intro-area .info .animation img {
    width: 180px;
  }
}

@media (max-width: 480px) {
  .intro-area {
    height: 660px;
  }

  .intro-area .intro {
    margin: 0;
  }

  .intro-area .info .animation {
    margin: 15px 0;
  }

  .intro {
    height: auto;
    margin-top: 45px;
  }

  .details {
    margin: 0;
  }

  .intro p {
    font-size: 16px;
  }

  .intro h1 {
    font-size: 36px;
  }

  .details ul li {
    margin: 0 3px;
    margin-top: 15px;
  }

  .details ul li p {
    font-size: 18px;
  }
}

@-webkit-keyframes guyAnimated {
  from {
    top: 0;
  } to {
    top: -15px;
  }
}

@keyframes guyAnimated {
  from {
    top: 0;
  } to {
    top: -15px;
  }
}

.about {
  display: flex;
  flex-wrap: wrap;
}

.about>* {
  flex: 1 1;
}

.about .title {
  margin-bottom: 25px;
  text-align: left;
}

.about p {font-size: 22px;line-height: 1.7;}

.about-video {
  align-self: center;
  text-align: center;
}

.about-action {
  margin-top: 25px;
}

.btn-ticket {
  background: #4caf50;
  color: #fff;
}

.btn-more {
  margin-left: 15px;
  background: #045fa4;
  color: #fff;
}

.action-area {
  overflow: hidden;
}

.action-area .title {
  margin-bottom: 25px;
  text-align: left;
}

.action {
  width: 640px;
  margin: 0 auto;
  text-align: center;
  z-index: 1;
}

.ticket-area {
  display: flex;
  flex-wrap: wrap;
}

.ticket-area>* {
  flex: 1 1;
}

form {
  margin: 0 auto;
  margin-top: 25px;
}

form button {
  margin-left: 5px;
}

@media (max-width: 768px) {
  .action {
    width: 100%;
    padding: 0 25px;
  }

  form {
    width: 100%;
    padding: 0 25px;
  }
}

.speaker-area {
  background: #ebebeb;
}

.speaker {
  position: relative;
}

.mark {
  position: relative;
  text-align: center;
  z-index: 1;
}

.mark p {font-size: 26px;font-weight: 700;text-transform: uppercase;}

.mark h4 {
  display: inline-block;
  background: #3e3e3e;
  color: #fff;
  padding: 5px 10px;
  margin: 15px 0;
  border-radius: 4px;
}

.mark.start-time h4 {
  background: #4caf50;
}

.mark.end-time h4 {
  background: #f44336;
}

.speaker::before {
  content: "";
  position: absolute;
  top: 50px;
  right: 50%;
  width: 3px;
  height: calc(100% - 100px);
  background: #3e3e3e;
  z-index: 0;
}

.speaker-area .single {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 45px 0;
}

.speaker-area .single::before {
  content: "";
  position: absolute;
  height: 15px;
  width: 15px;
  top: 6px;
  left: calc(50% - 9px);
  border-radius: 10px;
  background: #4caf50;
  border: solid 3px #3e3e3e;
  box-sizing: border-box;
}

.speaker-time h4 {
  position: relative;
  background: #045fa4;
  color: #fff;
  padding: 4px 15px;
  width: 100px;
  border-radius: 4px;
  z-index: 1;
}

.speaker-time h4::after {
  position: absolute;
  content: "";
  height: 100%;
  top: 0;
  left: -24px;
  width: 27px;
  z-index: -1;
  border: solid 13px transparent;
  border-right-color: #045fa4;
  box-sizing: border-box;
}

.speaker-area .single:nth-child(2n) .speaker-time h4 {
  margin-left: auto;
}

.speaker-area .single:nth-child(2n) .speaker-time h4:after {
  left: auto;
  left: initial;
  right: -24px;
  border-left-color: #045fa4;
  border-right-color: transparent;
}

.speaker-time p {
  margin-top: 5px;
}

.speaker-area .single>* {
  margin: 0 30px;
  width: calc(50% - 60px);
}

.speaker-area .single:nth-child(2n) {
  text-align: right;
  flex-direction: row-reverse;
}

.speaker-details {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  text-align: right;
  flex-direction: row-reverse;
}


.speaker-area .single:nth-child(2n) .speaker-details {
  text-align: left;
  flex-direction: row;
  justify-content: flex-start;
}

.speaker-details .image img {
  width: 100px;
  border-radius: 100%;
  border: solid 3px #ecdb43;
  padding: 5px;
}

.speaker-details .image {
  margin: 0 15px;
}

.speaker-details .info h4 {
  font-size: 36px;
}

@media (max-width: 991px) {

}

@media (max-width: 768px) {

}

@media (max-width: 480px) {

}

.sponsor-area {
  position: relative;
}

.sponsor-area .container {
  position: relative;
  z-index: 1;
}

.sponsor-area::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 20px;
  top: -20px;
  background-image: url(/static/media/wave.2332efc0.svg);
}

.sponsor {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.sponsor .single {
  width: calc(100%/3 - 30px);
  padding: 45px 25px;
  margin: 0 15px;
  text-align: center;
}

.sponsor .single img {
  width: calc(100% - 50px);
  -webkit-transition: 2s all;
  transition: 2s all;
}

@media (max-width: 991px) {
  .sponsor .single {
    padding: 45px 0px;
    padding-top: 0;
  }
}

@media (max-width: 991px) {
  .sponsor .single {
    padding: 25px 0px;
    padding-top: 0;
  }
}

@media (max-width: 480px) {
  .sponsor .single {
    width: calc(100%/2 - 30px);
  }
}

.footer-area {
  background: #2e2e2e;
  text-align: center;
  color: #fff;
}

.footer-area .container {
  padding: 50px 0;
}

.footer {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer .logo {
  margin-right: 15px;
}

.footer ul {
  padding: 25px 0;
}

.footer ul li {
  display: inline-block;
  margin: 0 5px;
}

.footer ul li.top a {
  background: #4caf50;
  padding: 5px 15px;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 4px;
}

.footer ul li a {
  color: #fff;
  text-decoration: none;
}

.event {
  display: flex;
  align-items: center;
}

.event h3 {
  color: #fff;
  font-size: 42px;
}

@media (max-width: 768px) {
  .footer {
    width: 100%;
    padding: 0 25px;
  }

  .footer .logo {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }

  .event h3 {
    font-size: 36px;
  }
}

@media (max-width: 480px) {
}


